<template>
  <div class="mt72">
    <div class="breaks" style="height:40px"></div>
    <div class="myBox">
      <div class="myBoxLeft">
        <subject />
        <div class="myBtn">
          <el-button class="elbtn" @click="toTools">新增资源</el-button>
          <el-button class="elbtn" @click="toResearchHomework">新增作业</el-button>
        </div>
      </div>
      <div class="myBoxRight">
        <div class="tupuBox">
          <div class="tupuShow">
            <img src="../../../../static/images/tupu.png" alt style="width:100%;height:100%" />
          </div>
        </div>
        <div class="titleBox">
          <p style="border-bottom:1px solid #d5efe6">
            <span style="padding:5px;background-color:#149385;color:#fff">成员列表</span>
          </p>
          <div class="myListBox">
            <div class="listBox" v-for="item in list" :key="item">
              <img src="../../../../static/images/头像.jpg" alt />
              <span>小陈</span>
            </div>
          </div>
          <div class="titleImgBox">
            <img src="../../../../static/images/作业列表.png" alt />
            <el-button class="change" size="small">修改</el-button>
          </div>
          <div class="table">
            <table>
              <thead>
                <tr style="background-color:#f8f8f8;">
                  <td style="color:#fb8b22;font-size:16px;padding:5px">序号</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">作业名称</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">题型</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">难度</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">来源</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">时间</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">操作</td>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(item, index) in lists"
                  :key="index"
                  style="margin-bottom:5px"
                  class="trHover"
                >
                  <td style="padding:10px;font-size:16px">{{ index+1 }}</td>
                  <td style="padding:10px;font-size:16px">
                    <a href="javascript:void(0);">内能</a>
                  </td>
                  <td style="padding:10px;font-size:16px">综合</td>
                  <td style="padding:10px;font-size:16px">中级</td>
                  <td style="padding:10px;font-size:16px">小陈老师</td>
                  <td style="padding:10px;font-size:16px">2019-11-11</td>
                  <td style="padding:10px;font-size:16px">
                    <el-button
                      style="background-color:#2ab8b3;color:#fff;border:0"
                      size="small"
                    >增加备用习题</el-button>
                  </td>
                </tr>
              </tbody>
            </table>
            <el-pagination
              layout="prev, pager, next"
              :total="100"
              class="page"
              style="color: #2ab8b3;"
            ></el-pagination>
            <!-- <paginationVue /> -->
          </div>
          <div class="titleImgBox" style="border-top:10px solid #eee;">
            <img src="../../../../static/images/资源列表.png" alt />
            <el-button class="change" size="small">修改</el-button>
          </div>
          <div class="table">
            <table>
              <thead>
                <tr style="background-color:#f8f8f8;">
                  <td style="color:#fb8b22;font-size:16px;padding:5px">序号</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">作业名称</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">题型</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">难度</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">来源</td>
                  <td style="color:#fb8b22;font-size:16px;padding:5px">时间</td>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(item, index) in list"
                  :key="index"
                  style="margin-bottom:5px"
                  class="trHover"
                >
                  <td style="padding:10px;font-size:16px">{{ index+1 }}</td>
                  <td style="padding:10px;font-size:16px">
                    <a href="javascript:void(0);">内能</a>
                  </td>
                  <td style="padding:10px;font-size:16px">综合</td>
                  <td style="padding:10px;font-size:16px">中级</td>
                  <td style="padding:10px;font-size:16px">小陈老师</td>
                  <td style="padding:10px;font-size:16px">2019-11-11</td>
                </tr>
              </tbody>
            </table>
            <el-pagination
              layout="prev, pager, next"
              :total="100"
              class="page"
              style="color: #2ab8b3;"
            ></el-pagination>
            <!-- <paginationVue /> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import subject from "../../../components/grade/subject.vue";
export default {
  components: {
    subject
  },
  data() {
    return {
      list: [1, 1, 1, 1, 1, 1, 1, 1],
      lists: [1, 1, 1, 1, 1, 1, 1, 1]
    };
  },
  methods: {
    toResearchHomework() {
      this.$router.push({
        path: "/homeCommon/TeachingResearch/ResearchHomeWork"
      });
    },
    toTools() {
      this.$router.push({ path: "/homeCommon/resourceTools" });
    }
  }
};
</script>
<style lang="less" scoped>
.myBox {
  width: 100%;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  .breaks {
    width: 100%;
    height: 40px;
    background-color: #f8f8f8;
  }
  .myBoxLeft {
    width: 270px;
    display: inline-block;
    border-right: 10px solid #eee;
    .myBtn {
      .elbtn {
        border: 0;
        margin: 0 auto;
        margin-bottom: 20px;
        background-color: #139583;
        color: #fff;
        display: block;
      }
    }
  }
  .myBoxRight {
    flex: 1;
    width: 1020px;
    display: inline-block;
    .tupuBox {
      width: 100%;
      padding: 40px;
      box-sizing: border-box;
      border-bottom: 10px solid #eee;
      .tupuShow {
        width: 100%;
        border: 1px solid #eee;
        height: 681px;
      }
    }
    .titleBox {
      padding: 10px 10px 0px 10px;

      .myListBox {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        border-bottom: 10px solid #f8f8f8;
        .listBox {
          width: 16.5%;
          height: 160px;
          text-align: center;
          //   border-bottom: 1px solid #eee;
          //   border-right: 1px solid #eee;
          border-bottom: 1px solid #eee;
          img {
            width: 60px;
            height: 60px;
            border-radius: 50%;
            margin: 0 auto;
            display: block;
            margin-top: 30px;
            margin-bottom: 10px;
          }
        }
      }
      .titleImgBox {
        height: 50px;
        img {
          margin-top: 15px;
        }
        .change {
          color: #fa9220;
          border: 1px solid #fa9220;
          float: right;
          margin-top: 15px;
          margin-right: 10px;
        }
      }
      .table {
        width: 100%;
        //  height: 50%;
        margin: 0 auto;
        text-align: center;
        //  border: 1px solid #2ab8b3;
        margin-bottom: 15px;
        table {
          width: 100%;
          height: 100%;
          // text-align: center;
          padding: 18px;
          .trHover:hover {
            background-color: #faf1ed;
          }
        }
        .page {
          position: relative;
          margin: 5px;
          right: -318px;
          bottom: 0;
        }
      }
    }
  }
}
</style>


